.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.external-css {
  font-family:  'roboto', sans-serif;
  font-weight: bold;
  text-shadow: 1px 1px blueviolet;
}


.button {
  display: flex;
  flex-direction: row;
  margin-top: 10%;
  margin-left: 21%;
}

.btn {
  background-color: coral;
  font-size: 20px;
  font-weight: bolder;
  font-family: 'roboto', sans-serif;
  height: 50px;
  width: max-content;
  border-radius: 15px;
}

.cls {
  margin-left: 30%;
}
.cls-component {
  height: 170px;
  width: 400px;
  background-color: aqua;
  border-radius: 20px;
}
.fun-component {
  height: 170px;
  width: 400px;
  background-color: yellowgreen;
  border-radius: 20px;
}